body {
	margin: 0;
	/*padding: 15px 0px;*/
	font-family: sans-serif;
	background: #0099CC;
	color: #fff;
	overflow-x: hidden;
}
#container{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	/*optional*/
	-ms-flex-align: center;
	    align-items: center;
}
.buttonGroup{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: left;
	    justify-content: left;
	/*optional*/
	-ms-flex-align: center;
	    align-items: center;
	margin-bottom: 15px;	
}
.container, #container{
	text-align: center;
	padding: 30px 15px 15px 15px;
}
h1{
	font-size: 20px;
	color: #19334d;
	-webkit-transform: scaleX(0.9);
	    -ms-transform: scaleX(0.9);
	        transform: scaleX(0.9);
	text-align: center;
	position: absolute;
	left: 0;
	right: 0;
	top: 40px;
	width: auto;
}
h2{
	font-size: 18px;
	font-weight: 300;
	text-align: center;
}
h3{

}
h4{
	font-size: 18px;
	margin-bottom: 0px;
}
button, select{
	cursor: pointer;
}
button{
	background: #fff;
	color: #000;
	font-size: 1.2em;
	padding: 0em 0.5em;
	border: 2px solid #000;
	border-radius: 5px !important;
	margin-right: 0.5em;
	-webkit-transition: 300ms all ease-in;
	-o-transition: 300ms all ease-in;
	transition: 300ms all ease-in;
}
button.quizBtn{
	border-top: 2px solid #000;
	border-right: 2px solid #000;
	border-left: 2px solid #000;
	border-bottom: 5px solid #000;
}
button:active, button:hover, button:focus{
	border-bottom: 2px solid #000;
	-webkit-box-shadow: 5px 5px 10px #000;
	        box-shadow: 5px 5px 10px #000;
}
button:hover{
	background: #333;
	color: #fff;
}
#rBtn, #soundBtn{
	margin: 0px 5px 5px 5px;
}



